<template>
  <div class="imChoose" @input="updateValue">
    <p>{{desc}}</p>
    <div class="ctrl">
      <span v-for="(choice , index) in choices" :key="choice.name"
        class="choice choosed" @click="toggle(index,$event)">
        {{choice.label}}
      </span>
    </div>
    <slot name="choice"
    v-for="(choice,index) in choosed"
    :choice="choice"
    :index="index">
      <!-- 这里写入备用内容 -->
    </slot>
  </div>
</template>

<script>
export default {
  name: 'imChoose',
  props: {
    choices: {

    },
    type: {

    },
    desc: {

    },
    value: {

    },
    updateValueFunc: {

    }
  },
  data () {
    return {
      choosed: [...this.choices],
      [this.type]: true
    }
  },
  // created () {
  //   // console.log(this.choosed)
  //   console.log(this.choosed === this.choices)
  // },
  methods: {
    toggle (index, event) {
      function toggleChoosed (vm, index, classList) {
        if (classList.contains('choosed')) {
          if (vm['least-one'] && vm.choosed.length <= 1) { return }
          vm.choosed.splice(index, 1)
          vm.choices[index].value = ''
          classList.remove('choosed')
        } else {
          vm.choosed.splice(index, 0, vm.choices[index])
          classList.add('choosed')
        }
      }
      // console.log(event.target.tagName)
      // console.log(event.target.className)
      toggleChoosed(this, index, event.target.classList)
      // console.log(event.target.className)
    },
    updateValue (event) {
      // console.log(event.target.dataset.index)
      this.value[event.target.dataset.index] = {
        name: this.choices[event.target.dataset.index].name,
        value: event.target.value
      }
      // console.log(this.value[event.target.dataset.index])
    }
  }
}
</script>

<style scoped lang="stylus">
.imChoose
  .ctrl
    .choice
      display inline-block
      margin 0 2px
      &.choosed
        background-color #3ba6f7
        color #fff
</style>



