<template>
  <header class="im-header">
    <span class="userimg"></span>
    <span class="username">秦俊杰</span>
  </header>
</template>

<script>
export default {
  name: 'imHeader',
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.im-header
  position fixed
  z-index 10
  width 100%
  padding: 0 16px;
  line-height 50px
  height 50px
  background-color white
  display flex
  align-items center
  .userimg
    // display inline-block
    width 30px
    height 30px
    background-color #eee
    border-radius 16px
    vertical-align center
    margin-right 5px
  .username
    margin-left 4px
    // border-top 20px solid black


</style>



