<template>
  <div class="im-module-box">
    <div class="title"> {{moduleTitle}} </div>
    <div class="content">
      <slot>
      暂时没有消息
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'imModuleBox',
  props: {
    moduleTitle: {
      type: String
    }
  },
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.im-module-box
  padding 10px
  margin 0 0 10px
  background-color white
  border-radius 10px
  box-shadow: 0 12px 30px -6px rgba(0,0,0,0.06)
  .title
    color #333
  .content
    height 100px
</style>



