<template>
  <div class="intern-apply">
    <form class="form" action="">
      <fieldset>
        <legend>学生基本信息</legend>
        <div class="form-item">
          <label for="student_name">学生姓名</label>
          <input type="text" id="student_name">
        </div>
        <div class="form-item">
          <label for="student_id">学号</label>
          <input type="text" id="student_id">
        </div>
        <div class="form-item">
          <label for="student_grade">年级</label>
          <input type="text" id="student_grade">
        </div>
        <div class="form-item">
          <label for="student_phonenumber">手机</label>
          <input type="text" id="student_phonenumber">
        </div>
        <div class="form-item">
          <label for="student_mail">邮箱</label>
          <input type="text" id="student_mail">
        </div>
        <template v-if="hasQQ">
          <div class="form-item">
            <label for="student_qq">QQ号</label>
            <input type="text" id="student_qq">
          </div>
        </template>
        <template v-else>
          <div class="form-item">
            <label for="student_wechat">微信号</label>
            <input type="text" id="student_wechat">
          </div>
        </template>
      </fieldset>
      <!-- <hr> -->
      <fieldset>
        <legend>实习单位情况</legend>
        <div class="form-item">
          <label for="student_mail">单位名称</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">单位地址</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">单位联系人姓名/职务</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">单位联系人电话/邮箱</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">实习薪酬</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">起始时间</label>
          <input type="text" id="student_mail">
        </div>
        <div class="form-item">
          <label for="student_mail">结束时间</label>
          <input type="text" id="student_mail">
        </div>
      </fieldset>

      <div class="form-item">
        <label for="student_mail">通过何种方式获得实习，请选择</label>
        <div class="form-item-radio">
          <input type="radio" id="radio_1" name="channel" value="Jack">
          <label for="radio_1">网招</label>
          <input type="radio" id="radio_2" name="channel" value="John">
          <label for="radio_2">校招</label>
          <input type="radio" id="radio_3" name="channel" value="Mike">
          <label for="radio_3">学院推荐</label>
          <input type="radio" id="radio_4" name="channel" value="Mike">
          <label for="radio_4">学长内推</label>
          <input type="radio" id="radio_5" name="channel" value="Mike">
          <label for="radio_5">朋友介绍</label>
          <label for="radio_another">其他：</label>
          <input type="text" name="channel">
        </div>
      </div>
      <div class="form-item">
        <label for="student_mail">申请理由（应修学分、已修学分，已修必修课和指定选修课程情况，参加教师项目情况，家长是否了解并支持离校实践，本人承诺等）：</label>
        <textarea name="" cols="30" rows="10"></textarea>
      </div>
      <div class="form-item">
        <label for="student_mail">修学情况确认（应修学分、已修学分，已修必修课和指定选修课程情况）：</label>
        <textarea name="" cols="30" rows="10"></textarea>
      </div>
      <div class="form-item">
        <label>是否被老师安排有工作/</label>
        相关老师：
        <input type="text">
      </div>
      <input class="button submit" type="button" value="提交实习申请">
    </form>

  </div>
</template>



<script>
export default {
  name: 'InternApply',
  data () {
    return {
      hasQQ: false
    }
  }
}
</script>

<style scoped lang="stylus">
.intern-apply
  background-color white
  min-height 80%
  margin 5px 10px 0
  padding 16px
  .form
    color #48576a
    .button.submit
      width 160px
      height 30px
      margin-left 100px
      background-color #00a1d6!important
      border 1px solid #c4c4c4
      border-color #20a0ff
      border-radius 4px
      color #fff
    .form-item
      margin-bottom 10px
      label
        width 80px
        line-height 30px
        margin-right 20px
        text-align right
        display inline-block
        border-width 1px!important
      input
        line-height 26px
        margin 2px 0
        padding 0 5px
        border-radius 4px
        border: 1px solid #bfcbd9;
</style>



