<template>
  <aside class="im-side-bar">
    <header>
      <span class="userimg"></span>
      <span class="username">秦俊杰</span>
    </header>
    <nav>
      <slot>
      插点内容啊
      </slot>
    </nav>
  </aside>
</template>

<script>
export default {
  name: 'imSideBar',
  data () {
    return {}
  }
}
</script>

<style scoped lang="stylus">
.im-side-bar
  background-color white
  width 200px
  height 100vh
  position absolute
  left -200px
  // top -50px
  z-index 100
  transition: transform .3s;
  header
    display flex
    height 120px
    flex-direction column
    align-items center
    justify-content flex-end
    .userimg
      width 50px
      height 50px
      background-color #eee
      border-radius 25px
    .username
      margin-bottom 10px
      margin-top 10px
</style>



