<template>
  <div class="container">
    <form action="/student/informationsubmit.htm" method="post">
      <template v-for="item in form">

        <div v-if="item.el.tag==='input'" :key="item.name">
          <label for=" item.id?item.id:'' ">{{item.label}}</label>
          <input  :type="item.el.type" :name="item.name" v-model="item.value">
        </div>
        <div v-else-if="item.el.tag==='select'" :key="item.name">
          <label for=" item.id?item.id:'' ">{{item.label}}</label>
          <select :name="item.name" v-model="item.value">
            <option v-for="option in item.el.options" :value="option.value" :key="option.value">{{option.text}}</option>
          </select>
          <p>{{item.value}}</p>
        </div>
<!--  -->
        <choose v-else-if="item.el.tag==='choose'" :key="item.name"
          :choices="item.el.choices"
          :type="item.el.type"
          :desc="item.el.desc"
          :value="item.value"
          :updateValueFunc="item.updateValueFunc">
          <div
            slot="choice"
            slot-scope="{choice,index}">
            <label>{{choice.label}}</label>
            <!-- <input :type="choice.el.type" :name="choice.name" :value="choice.value"> -->
            <input :type="choice.el.type" :name="choice.name" v-model="choice.value" :data-index="index">
            <p>{{choice.value}}</p>
          </div>
        </choose>

      </template>
      <!-- <input type="submit" name="v" value="修改个人信息"> -->
      <button type="button" @click="submitStudentInfo">修改个人信息</button>
    </form>
  </div>
</template>

<script>
import choose from './imChoose'
export default {
  components: {choose},
  name: 'StudentInfo',
  data () {
    return {
      form: [
        {
          name: 'name',
          value: '',
          label: '姓名',
          el: {
            tag: 'input',
            type: 'text'
          }
        },
        {
          name: 'id',
          value: '',
          label: '学号',
          el: {
            tag: 'input',
            type: 'text'
          }
        },
        {
          name: 'grade',
          value: '',
          label: '年级',
          el: {
            tag: 'select',
            type: 'single',
            options: [
              {
                value: 1.1,
                text: '大一'
              },
              {
                value: 1.2,
                text: '大二'
              },
              {
                value: 1.3,
                text: '大三'
              },
              {
                value: 1.4,
                text: '大四'
              },
              {
                value: 2.1,
                text: '研一'
              },
              {
                value: 2.2,
                text: '研二'
              },
              {
                value: 2.3,
                text: '研三'
              },
              {
                value: 2.4,
                text: '研四'
              }
            ]
          }
        },
        // 手机号
        {
          name: 'tel',
          value: '',
          label: '手机',
          el: {
            tag: 'input',
            type: 'tel'
          }
        },
        {
          name: 'email',
          value: '',
          label: '邮箱',
          el: {
            tag: 'input',
            type: 'email'
          }
        },
        {
          name: 'qqorwechat',
          value: [],
          // updateValueFunc: function (vm, event) {
          //   // console.log(event.target.dataset.index)
          //   vm.value[event.target.dataset.index] = event.target.value
          // },
          label: 'QQ号/微信',
          el: {
            tag: 'choose',
            type: 'least-one',
            desc: '请至少填写qq、微信中一个',
            choices: [
              {
                name: 'qq',
                value: '',
                label: 'QQ',
                el: {type: 'number'}
              },
              {
                name: 'wechat',
                value: '',
                label: '微信',
                el: {type: 'text'}
              }
            ]
          }
        }
      ]
    }
  },
  methods: {
    submitStudentInfo () {
      var gerData = (data, items) => {
        // let data = {}
        for (const item of items) {
          if (item.value instanceof Array) {
            gerData(data, item.value)
          } else {
            console.log(item.name + ':' + item.value)
            data[item.name] = item.value
          }
        }
        return data
      }

      this.$axios({
        method: 'post',
        url: '/student/informationsubmit.htm',
        headers: {
          'X-Requested-With': 'XMLHttpRequest'
        },
        data: gerData({}, this.form),
        timeout: 1000
      }).then((response) => {
        if (response.status === 200) {
          console.log(response.data)
        }
      }).catch((err) => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped lang="stylus">

</style>



